@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato|Ubuntu:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sarabun&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: whitesmoke;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  padding: 1em;
  border: none;
  font-family: 'Ubuntu', sans-serif;
  font-size: 1.2em;
}

input:focus {
  outline: none;
}

label {
  margin: 0.5em;
  font-size: 1.4em;
}

.input {
  display: grid;
  border: 1px solid lightgray;
  margin: 1em;
  border-radius: 0.2em;
}

button {
  border-radius: 0.2em;
  box-shadow: 5px 2px 10px 3px #494848;
  font-family: 'Lato', sans-serif;
  padding: 1em;
  margin: 0.25em;
  text-decoration: none;
  border: none;
  -webkit-transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease, -webkit-transform 150ms ease;
  cursor: pointer;
}

button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}

button:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
}

a, a:hover, a:visited, a:link, a:active {
  text-decoration: none;
}
* {
  box-sizing: border-box;
}
.homeImage {
  position: relative;
}

.img {
  height: 43em;
  background-image: url(/static/media/home1.7c7adde0.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  align-items: center;
  display: flex;
  flex-direction: column;
}
.img:before {
  content: '';
  position: absolute;
  top: 4em;
  right: 0;
  bottom: 4em;
  left: 0;
  height: 43em;
  background-image: -webkit-gradient(linear, left top, right bottom, from(#39333bb7), to(#180c27ab));
  background-image: linear-gradient(to bottom right, #39333bb7, #180c27ab);
  opacity: 0.6;
}

.title {
  padding: 1em 0.5em;
  font-size: 3em;
  color: white;
  text-align: center;
  font-family: 'Raleway';
  position: relative;
}

.paragraph {
  color: white;
  font-size: 1.5em;
  position: relative;
  padding: 1em 2em;
  text-align: justify;
}

.header {
  height: 4em;
  display: grid;
  grid-template-columns: 80px 200px 100px 100px;
  grid-template-rows: auto;
  grid-gap: 5px;
  gap: 5px;
  align-items: center;
  font-family: 'Ubuntu', sans-serif;
  background-color: rgb(44, 44, 44);
  color: azure;
}

.logo {
  height: 6.5em;
  font-size: 10px;
}

.slogan {
  font-size: 2em;
  text-align: center;
}

.links {
  text-align: center;
}

.submit {
  width: 12em;
  font-size: 1.5em;
  color: azure;
  background-color: #402f80;
  position: relative;
}

.submit:hover,
.submit:focus {
  background: #1c0444;
}

@media(min-width: 992px) {
  .title {
    padding: 2em 2em 1em 2em;
  }

  .paragraph {
    font-size: 2em;
    padding: 1em 4em;
  }
}
.no-suggestions {
  color: #999;
  padding: 0.5rem;
}

.suggestions {
  border: 1px solid #999;
  border-top-width: 0;
  list-style: none;
  margin-top: 0;
  max-height: 143px;
  overflow-y: auto;
  padding-left: 0;
  width: calc(300px + 1rem);
}

.suggestions li {
  padding: 0.5rem;
}

.suggestion-active,
.suggestions li:hover {
  background-color: #282c36;
  color: #ffffff;
  cursor: pointer;
  font-weight: 700;
}

.suggestions li:not(:last-of-type) {
  border-bottom: 1px solid #999;
}

.question {
  color: whitesmoke;
  background: rgb(44, 44, 44);
  font-size: 2em;
  text-align: center;
  padding-top: 2%;
  padding-bottom: 2%;
  margin: 1%;
  font-family: "Raleway";
}
.top {
  border-width: 0.5px;
  border-right: 0;
  border-left: 0;
  border-color: rgb(190, 176, 176);
  border-style: solid;
  margin-top: 1em;
  padding: 1em 1.5em 0.5em 1.5em;
}

.quizTitle {
  color: whitesmoke;
  background-color: #402f80;
  text-align: center;
  font-family: "Raleway";
  padding: 0.2em;
}

.select {
  padding: 1em;
  border: none;
  font-family: 'Ubuntu', sans-serif;
  font-size: 1.2em;
}

.select:focus {
  outline: none;
}

.middle {
  display: flex;
  justify-content: center;
}
.error {
  font-size: 4em;
  font-style: italic;
  text-align: center;
}

.restaurantTitle {
  font-size: 2em;
  padding: 0.5em;
  color: white;
  background-color: #402f80;
  text-align: center;
  font-family: 'Sarabun', sans-serif;
}

.restaurantIMG {
  object-fit: cover;
  height: 15em; 
  width: 20em;
  margin: auto;
}

.restaurantContainer {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  border-width: 0.5px;
  border-color: rgb(190, 176, 176);
  border-style: solid; 
  box-shadow: 5px 4px 6px 0px rgba(184,177,184,1); 
}

.openText {
  color: rgb(10, 83, 10);
  font-family: Impact;
  font-size: 1em;
}

.closeText {
  color: rgb(139, 30, 30);
  font-family: Impact;
  font-size: 1em;
}

.list_mapToggle {
  background-color: rgb(44, 44, 44);
  height: 2em;
  display: flex;
  justify-content: center;
}

.lmbutton {
  color: white;
  margin-right: 0.5em;
}

.lbutton {
  color: white;
  margin-left: 1em;
}

.list-group {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
}

p {
  margin: 0;
  padding: 0;
}

.category {
  margin: 1em;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif ;
  font-size: 1.2em;
  color: black;
}

.resultsTitle {
  color: whitesmoke;
  background-color: #402f80;
  text-align: center;
  font-family: "Raleway";
  padding: 0.2em;
  margin-bottom: 0em;
}

.grid {
  padding: 1em;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
  grid-row-gap: 5px;
  grid-column-gap: 5px;
}

#popup, .MuiPaper-root {
  display: none;
}

#cave {
  margin-bottom: 1em;
  box-shadow: 3px 3px 5px 0px rgba(184,177,184,0.75);
  background-color: #f16262;
  color: azure;
  width: 5em;
  font-size: 1.3em;
  padding: 0.3em;
}

.cave {
  display: flex;
  justify-content: center;
}

#cave:hover,
#cave:focus,
#popup:hover,
#popup:focus {
  background: #830b0b;
}

@media (min-width: 768px) {
  .restaurantTitle{
    font-size: 2.8vw;
  }

  .grid {
    grid-template-columns: auto auto;
    grid-template-rows: auto;
  }

  .MuiPaper-root {
    display: block;
  }

  #popup {
    display: block;
    margin: auto;
    margin-bottom: 1em;
    box-shadow: 3px 3px 5px 0px rgba(184,177,184,0.75);
    background-color: #f16262;
    color: azure;
    width: 5em;
    font-size: 2.4vw;
    padding: 0.3em;
  }

  #cave, .collapse {
    display: none;
  }

}

@media (min-width: 992px) {
  .restaurantTitle{
    font-size: 2.4vw;
  }

  #popup {
    font-size: 2vw;
  }
}

@media (min-width: 1200px) {
  .restaurantTitle{
    font-size: 1.9vw;
  }
  .grid {
    grid-template-columns: auto auto auto;
    grid-template-rows: auto;
  }
  #popup {
    font-size: 1.5em;
  }
}
